import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
// import * as data from './data.json';
// import 'rxjs/add/operator/map';
// import { map } from 'rxjs/add/operator';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ProfileService {

  private username: string;
  private clientid = '286820312';
  private clientsecret = 'https://api.github.com/user/repos';

  constructor(private http: HttpClient) {
    this.username = "{{username}}";
  }
  getProfileInfo() {
    return this.http.get("https://api.github.com/users/" + this.username).pipe(map(res =>{
      return res;
    }));
  }
  getProfileRepos() {
    return this.http.get("https://api.github.com/users/" + this.username + "/repos?client_id=" + this.clientid + "&client_secret=" + this.clientsecret).pipe(map(res => {
      return res;
    }));
  }
  updateProfile(username: string) {
    this.username = username;
  }
}
